<template>
  <div class="account">
    <div class="account-inner">
      <div class="title">
        <h1>{{$t('account.title')}}</h1>
      </div>

      <div class="userinfo">
        <div class="people">
          <div class="avatar"></div>
          <dl>
            <dt>{{$t('account.username')}}</dt>
            <dd>{{userInfo.username}}</dd>
          </dl>
        </div>
        <ul>
          <li>
            <dl>
              <dt>{{$t('account.accounttate')}}：</dt>
              <dd>{{userInfo.type}}</dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>{{$t('account.endTime')}}：</dt>
              <dd>{{userInfo.endTime}}</dd>
            </dl>
          </li>
        </ul>
      </div>

      <div class="modify-pass">
        <el-form class="pass-form" :model="passForm" :rules="passRules" ref="passForm">
          <div class="form-col">
            <div class="form-column">
              <span class="title">{{$t('account.telTitle')}}</span>
              <el-form-item :label="$t('account.telPlaceholder')" prop="username"
                            :class="{'noLabel': !passForm.username, 'onFocus':passType.username === 1}" class="tel">
                <el-input type="text" v-model="passForm.username" :placeholder="$t('account.telPlaceholder')"
                          @focus="passType.username = 1" @blur="passType.username = 0"></el-input>
              </el-form-item>
            </div>

            <div class="form-column">
              <span class="title">{{$t('account.codeTitle')}}</span>
              <el-form-item :label="$t('account.codePlaceholder')" prop="verificationCode"
                            :class="{'noLabel': !passForm.verificationCode, 'onFocus':passType.verificationCode === 1}"
                            class="code">
                <el-input type="text" v-model="passForm.verificationCode" :placeholder="$t('account.codePlaceholder')"
                          @focus="passType.verificationCode = 1" @blur="passType.verificationCode = 0"></el-input>
              </el-form-item>
              <el-button type="primary" class="send-code" :disabled="!isCode || !isUsername" @click="sendCode">
                <span v-show="!countShow">
                <template v-if="isFirstSend">{{$t('account.sendCode')}}</template>
                <template v-else>{{$t('account.sendCodeRe')}}</template>
              </span>
                <span v-show="countShow">{{count}} 秒</span>
              </el-button>
            </div>
          </div>

          <div class="form-col">
            <div class="form-column">
              <span class="title">{{$t('account.newPassTitle')}}</span>
              <el-form-item :label="$t('account.newPassPlaceholder')" prop="password"
                            :class="{'noLabel': !passForm.password, 'onFocus':passType.password === 1}"
                            class="new-pass">
                <el-input type="text" v-model="passForm.password" :placeholder="$t('account.newPassPlaceholder')"
                          @focus="passType.password = 1" @blur="passType.password = 0"></el-input>
              </el-form-item>
            </div>
          </div>

          <div class="form-col">
            <div class="form-column">
              <span class="title">{{$t('account.checkPassTitle')}}</span>
              <el-form-item :label="$t('account.checkPassPlaceholder')" prop="checkPassword"
                            :class="{'noLabel': !passForm.checkPassword, 'onFocus':passType.checkPassword === 1}"
                            class="check-pass">
                <el-input type="text" v-model="passForm.checkPassword" :placeholder="$t('account.checkPassPlaceholder')"
                          @focus="passType.checkPassword = 1" @blur="passType.checkPassword = 0"></el-input>
              </el-form-item>
            </div>
          </div>

          <el-button type="primary" class="pass-submit" :disabled="!isPassword || !isCheckPassword"
                     @click="submitModify">{{$t('account.modifyPass')}}
          </el-button>
        </el-form>

      </div>
    </div>
  </div>
</template>

<script>
import { isvalidUsername, isvalidUserpassword } from 'common/js/validate'

const TIME_COUNT = 59

export default {
  name: 'Account',
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        this._data.isUsername = false
        callback(new Error(' '))
      } else {
        this._data.isUsername = true
        callback()
      }
    }
    const validateCode = (rule, value, callback) => {
      if (value.length !== 4) {
        this._data.isCode = false
        callback(new Error(' '))
      } else {
        this._data.isCode = true
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isPassword = false
        callback(new Error(' '))
      } else {
        this._data.isPassword = true
        callback()
      }
    }
    const validateCheckPass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else if (value !== this.passForm.password) {
        this._data.isCheckPassword = false
        callback(new Error(' '))
      } else {
        this._data.isCheckPassword = true
        callback()
      }
    }
    return {
      userInfo: {
        username: '13666666666',
        type: 'VIP会员',
        endTime: '2018-05-22 10:15:42'
      },
      passForm: {
        username: '',
        verificationCode: '',
        password: '',
        checkPassword: ''
      },
      passRules: {
        username: [
          {required: true, trigger: 'blur', validator: validateUsername},
          {required: true, trigger: 'change', validator: validateUsername}
        ],
        verificationCode: [
          {required: true, trigger: 'blur', validator: validateCode},
          {required: true, trigger: 'change', validator: validateCode}
        ],
        password: [
          {required: true, trigger: 'blur', validator: validatePass},
          {required: true, trigger: 'change', validator: validatePass}
        ],
        checkPassword: [
          {required: true, trigger: 'blur', validator: validateCheckPass},
          {required: true, trigger: 'change', validator: validateCheckPass},
        ]
      },

      passType: {
        username: '',
        verificationCode: '',
        password: '',
        checkPassword: ''
      },

      isUsername: '',
      isCode: '',
      isPassword: '',
      isCheckPassword: '',

      isFirstSend: true, // 判断是不是没点过发送，因为提示的文字不一样
      count: '', // 内部显示的数字
      countShow: false, // 判断显示的是文字提示还是倒计时数字
      timer: null  // 计时器
    }
  },
  computed: {},
  created () {
    console.log('account')
  },
  methods: {
    submitModify () {
      // this.$store.dispatch()
      var test = {
        "Type": "Rectangle",
        "widght": 20,
        "height": 40
      }
      window.core.notice(test)
    },
    // 发送验证码
    sendCode() {
      if(!this.timer) {
        this.count = TIME_COUNT
        this.countShow = true
        this.isFirstSend = false
        // ajax调验证码接口位置

        // 定时器60秒
        this.timer = setInterval(() => {
          if(this.count > 0 && this.count <= TIME_COUNT) {
            this.count--
          } else {
            this.countShow = false
            clearInterval(this.timer)
            this.timer = null
          }
        }, 1000)
      }
    }
  },
  destroyed() {
    clearInterval(this.timer)
    this.timer = null
  },
  components: {}
}


</script>

<style rel="stylesheet/scss" lang="scss">
@import "common/styles/variable.scss";
/* reset element-ui css */
.pass-form {
  display: flex;
  flex-direction: column;
  .el-form-item {
    border: none;
    margin-bottom: 0;
    padding-top: 5px;
    .el-form-item__content {
      line-height: 30px;
    }
    &.noLabel label {
      visibility: hidden;
    }
    & label::before {
      display: none;
    }
    .el-input {
      display: inline-block;
      width: 100%;
      height: 30px;
      line-height: 30px;
      input {
        background: transparent;
        border: 0px;
        -webkit-appearance: none;
        border-radius: 0px;
        padding: 0 5px 0 0;
        color: $gray_dark;
        height: 30px;
        font-size: 16px;
        border-bottom: 1px solid $gray_line;
        &::-webkit-input-placeholder {
          color: $gray_placeholder;
        }
      }
    }
    .el-input__inner:focus {
      border-bottom: 1px solid $gray_line_on;
    }
    label {
      color: $gray_placeholder;
      font-size: 12px;
      line-height: 12px;
      padding: 0 0 0 0;
    }
    &.is-error {
      label {
        visibility: visible;
        color: $red;
      }
      .el-input__inner {
        border-bottom: 1px solid $red;
      }
      .el-input__inner:focus {
        border-bottom: 1px solid $gray_line_on;
      }
    }
    &.onFocus {
      label {
        visibility: visible;
        color: $gray_line_on;
      }
    }
  }
  .form-col {
    .form-column {
      .send-code {
        flex: 0 0 96px;
        width: 96px;
        height: 30px;
        padding: 0;
        font-size: 12px;
        background: $gray_line_on;
        border: none;
        margin-left: 12px;
      }
      .is-disabled {
        background: $gray_line;
        color: $gray_light;
      }
    }
  }
  .pass-submit {
    width: 120px;
    height: 42px;
    background: $gray_line_on;
    border: none;
    margin-top: 12px;
    &.is-disabled {
      background: $gray_line;
      color: $gray_light;
    }
  }
}
</style>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.baima-container {
  .account {
    flex: 0 0 calc(100% - 48px);
    display: flex;
    width: 100%;
    height: calc(100% - 48px);
    padding: 20px 48px 28px 48px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12) inset;
    .account-inner {
      flex: 1;
      width: 100%;
      display: flex;
      flex-direction: column;
      .title {
        flex: 1 0 80px;
        height: 80px;
        background: $white;
        display: flex;
        justify-content: center;
        align-items: center;
        h1 {
          font-size: 24px;
          color: $gray_dark;
          text-align: center;
        }
      }
      .userinfo {
        margin-top: 12px;
        flex: 1 0 168px;
        height: 168px;
        background: $white;
        padding: 20px 36px 8px 36px;
        display: flex;
        flex-direction: column;
        .people {
          flex: 1 0 48px;
          width: 100%;
          height: 48px;
          display: flex;
          align-items: center;
          margin-bottom: 12px;
          .avatar {
            flex: 0 0 48px;
            width: 48px;
            height: 48px;
            background: url("../../images/img-avatar.png") no-repeat left top;
            margin-right: 24px;
            cursor: pointer;
          }
          dl {
            display: flex;
            flex-direction: column;
            justify-content: center;
            dt {
              font-size: 16px;
              color: $gray;
              margin-bottom: 6px;
            }
            dd {
              font-size: 20px;
              font-weight: bold;
              color: $gray_dark;
            }
          }
        }
        ul {
          flex: 1 0 80px;
          width: 100%;
          height: 80px;
          display: flex;
          flex-direction: column;
          li {
            flex: 0 0 40px;
            height: 40px;
            dl {
              display: flex;
              flex-direction: row;
              height: 16px;
              line-height: 42px;
              dt {
                color: $gray_light;
              }
              dd {
                color: $gray_dark;
                padding-left: 26px;
              }
            }
          }
        }
      }
      .modify-pass {
        margin-top: 12px;
        flex: 1 0 222px;
        height: 222px;
        background: $white;
        padding: 8px 36px 20px 36px;
        display: flex;
        .pass-form {
          width: 100%;
          .form-col {
            flex: 1;
            display: flex;
            clear: both;
            .form-column {
              flex: 1;
              display: flex;
              align-items: flex-end;
              .title {
                flex: 0 0 98px;
                width: 98px;
                height: 30px;
                padding: 0;
                align-items: flex-end;
                justify-content: flex-start;
                line-height: 40px;
                font-size: 16px;
                color: $gray_light;
              }
              .tel {
                flex: 1 0 202px;
                width: 202px;
                margin-right: 36px;
              }
              .code {
                flex: 1 0 140px;
                width: 140px;
              }
              .new-pass {
                width: 100%;
              }
              .check-pass {
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
}
</style>



